

* {
  box-sizing: border-box;
  
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  margin-left: 50px;
  margin-top: 50px;
  
}



#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
* {
  box-sizing: border-box;
}


li {
  list-style: none;
  font-size: 30px;
  font-weight: 400;
  align-items: center;
  margin-bottom: 5px;
}
ul {
  padding-left: 0px;
}
p {
  font-size: 30px; 
  font-weight: 400;
  margin: 0;
  margin-top: 15px;
}

span {
  font-weight: 400;
}
img {
  padding-right: 5px;
}

input {
  width: 400px;
  border-radius: 10px;
  height: 40px;
  background-image:linear-gradient(to bottom right, rgb(174, 195, 250), rgb(250, 250, 179))
}
